*{
  margin:0;
  padding:0; 
  box-sizing: border-box;
  list-style: none; 
  text-decoration: none;
  list-style-type: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
  font-family: 'Lato',
   sans-serif; font-family: 'Noto Sans KR',
   sans-serif;
}
p,a,h1,h2,h3,h4,h5,span{
  color: #333;
}

/* header 영역입니다. */
#contents{
  position: relative;
  margin: 0 auto; 
  width: 100%;
  padding: 0 15px;
}
.navbar{
  display: none;
}
#header{
  width: 100%;
  position: fixed;
  line-height: 25px;
  left:0;
  top: 0;
  display: inline-block;
  z-index: 100;
  padding: 10px 0;
  text-align: left;
  background: #fff;
}
#logo{
  font-size: 20px;
  position:relative;
  left: 15px;
}
#openMenu{
  position: absolute;
  right: 15px; 
  top: 10px;
  font-size: 22px; 
  z-index: 99;
  transition: .5s ease;
}

.sideMenu{

  width: 100%;
  z-index: 2;
  height: 100vh;
  position: fixed;
  right: -100%;
  top: 0;
  background:#f33;
  animation-name: slide;
  animation-duration: .5s;
}
.showMenu{
  width: 100%;
  z-index: 2;
  height: 100vh;
  position: fixed;
  right: 0;
  top: 0;
  background:#f33;
  animation-name: slide;
  animation-duration: .5s;
}
@keyframes slide {
  0%{
    right:-100%;
  }
  100%{
    right: 0;
  }
}
.list-wrap{
  margin-top: 100px;
  transition: all 1s;
}
.list-menu{
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  
  /* right: -300px; */

}
.list-menu li{
  padding: 10px 0;
}
/* header 영역입니다. */



/* footer 영역입니다. */

#footer{
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  font-size: 12px;
  text-align: left;
}
#footer .footer-wrap{
  width: 50%;
}
#footer a{
  text-decoration: underline;
}
/* footer 영역입니다. */



/* main 영역입니다. */
#section {
  margin: 70px 0;
}
#section .main-box-wrap{
  display: flex;
  flex-direction: column;
}
#section .main-box-wrap .main-box{
  width: 100%;
  margin-bottom: 20px;
}
#section .main-box-wrap .main-box img{
  width: 100%;
}
#section .main-box-wrap .main-box .main-text{
  font-weight: 400;
  text-align: center;
}

@media(min-width:820px){
  #section .main-box-wrap{
    flex-direction: row;
  }
  #logo{
    padding: 15px 0;
  }
  #section .main-box-wrap .main-box{
    position: relative;
    width: 50%;
    padding: 10px;
    margin: 0;
  }
  #section .main-box-wrap .main-box:nth-child(1){
    padding-left: 0;
  }
  #section .main-box-wrap .main-box:nth-child(2){
    padding-right: 0;
  }
  #section .main-box-wrap .main-box .main-text{
    position: absolute;
    top: 50%;
    transform: translateX(-50%); left: 50%;
    display: none;
    font-size: 35px;
    font-weight: 600;
  }
  #section .main-box:hover .main-text{
    color:#000;
    display: block;
    transition: all 1s ease;
    
  }
  #section .main-box img:hover{
    transition: opacity .2s ease-in-out;
    opacity: 0.2;
  }
}
/* main 영역입니다. */


/* about 영역입니다. */
#about-section {
  margin: 80px auto;
  max-width: 1000px;

}
#about-section .main-box-wrap{
  display: flex;
  flex-direction: row;

}
#about-section .main-box-wrap .main-box{
  width: 50%;
  padding: 10px;
}
#about-section .main-box-wrap .main-box:nth-child(1){
  padding-left: 0;
}
#about-section .main-box-wrap .main-box:nth-child(2){
  padding-right: 0;
}
#about-section .main-box-wrap .main-box img{
  width: 100%;
}
#about-section .main-box-wrap .main-box .main-text{
  font-weight: 500;
  text-align: center;
  font-size: 18px;
}
.panel-question {
  height: 100%;
  background-color: #fff;
  font-size: 11px;
  border: 1px solid #000;
  text-align: left;

}

.panel-heading {
  font-weight: bold;
  color: #333;
  padding-left: 10px;
  cursor: pointer;
  position: relative;
  font-size: 35px;
}
.panel-heading .panel-toggle{
  position: absolute;
  right: 10px;
}

.panel-body {
  padding: 15px 0 30px 0;
  width: 100%;
}
.panel-body p{
  font-size: 22px;
  padding: 10px;
}
.panel-body img{width: 100%;}
.cate-img{
  display: flex;
  flex-wrap: wrap;
}
.cate-fashion{
  display: flex;
  flex-wrap: wrap;
}

.cate-img img{
  width: 33.333%;
  padding: 4px;
}

.cate-fashion img{
  width: 50%;
}

@media(min-width:820px){
  #about-section .main-box-wrap{
    flex-direction: row;
  }
  #about-section .main-box-wrap .main-box{
    position: relative;
    width: 50%;
    padding: 10px;
    margin: 0;
  }
  #about-section .main-box-wrap .main-box:nth-child(1){
    padding-left: 0;
  }
  #about-section .main-box-wrap .main-box:nth-child(2){
    padding-right: 0;
  }
  #about-section .main-box-wrap .main-box .main-text{
    position: absolute;
    top: 50%;
    transform: translateX(-50%); left: 50%;
    display: none;
    font-size: 30px;
    font-weight: 600;
  }
  #about-section .main-box:hover .main-text{
    color:#000;
    display: block;
    transition: all 1s ease;
    
  }
  #about-section .main-box img:hover{
    transition: opacity .2s ease-in-out;
    opacity: 0.2;
  }
  .panel-body p{
    font-size: 27px;
  }
}
/* about 영역입니다. */


@media(min-width:980px){

  .menu-wrap{
    display: none;
  }
  #logo{
    font-size: 30px;
  }
  .navbar{
    display: inline-block;
    position: absolute;
    right: 15px;
    top: 27px;
  }
  .navbar ul li {
    float: left;
    padding-left: 50px;
  }
  #footer{
    font-size: 15px;
  }
  #footer .footer-wrap{
    width: 30%;
  }
}

